
html,
.container {
  width: 100%;
}
* {
  margin: 0;
  padding: 0;
}
.container {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: justify;
  font-size: 0.28rem;
  color: #333;
}
.container img {
  pointer-events: none;
}
.content-img {
  width: 100%;
}
.content-img img {
  width: 100%;
}
.btn-img {
  width: 2.07rem;
  height: 0.83rem;
  position: fixed;
  bottom: 0.6rem;
  left: 50%;
  margin-left: -1.03rem;
}
.btn-img img {
  width: 100%;
